import Head from 'next/head';
import { useState } from 'react';
import dynamic from 'next/dynamic';
import ChatForm from '../components/ChatForm';
import ChatResponse from '../components/ChatResponse';
import ChatHistoryPane from '../components/ChatHistoryPane';
import fetchChat from '../utils/fetchChat';

const ChatInterface = dynamic(() => import('../components/ChatInterface'), {
  ssr: false,
});

export default function Home() {
  const [messages, setMessages] = useState([]);
  const [history, setHistory] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [activeChatIndex, setActiveChatIndex] = useState(null);

  const handleSendMessage = async (newMessage) => {
    setMessages((prevMessages) => [...prevMessages, { text: newMessage, isUser: true }]);
    setIsLoading(true);

    try {
      const response = await fetchChat(newMessage);
      setMessages((prevMessages) => [...prevMessages, { text: response, isUser: false }]);
    } catch (error) {
      console.error("Error fetching chat response:", error);
      setMessages((prevMessages) => [...prevMessages, { text: "Sorry, I encountered an error.", isUser: false }]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSelectChat = (index) => {
    setActiveChatIndex(index);
    setMessages(history[index].messages);
  };

  const handleNewChat = () => {
    setMessages([]);
    setActiveChatIndex(null);
  };

  const handleSaveHistory = () => {
    if (messages.length > 0) {
      setHistory((prevHistory) => [
        ...prevHistory,
        { title: `Chat #${history.length + 1}`, messages },
      ]);
    }
  };

  return (
    <div className="flex">
      <Head>
        <title>Next.js Chatbot</title>
        <meta name="description" content="A simple chatbot UI built with Next.js" />
        <link rel="icon" href="/favicon.ico" />
      </Head>

      <ChatHistoryPane history={history} onSelect={handleSelectChat} />

      <div className="flex-1">
        <div className="p-4">
          <button onClick={handleNewChat} className="p-2 bg-[#66fcf1] text-black rounded mb-4">
            New Chat
          </button>
          <div>
            {messages.map((message, index) => (
              <ChatResponse key={index} message={message.text} isUser={message.isUser} />
            ))}
            {isLoading && <ChatResponse message="Thinking..." isUser={false} />}
          </div>
          <ChatForm onSendMessage={handleSendMessage} disabled={isLoading} />
          {messages.length > 0 && !isLoading && (
            <button
              onClick={handleSaveHistory}
              className="mt-4 p-2 bg-[#66fcf1] text-black rounded"
            >
              Save Chat
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

